@import "./variables";

.toolbar {
    height: $tooblar-height;
    background-color: white;
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    box-shadow: 0 4px 5px gray;
    z-index: 10;

    &__btn {
        width: 25px;
        height: 25px;
        font-size: 18px;
        border: none;
        outline: none;
        cursor: pointer;
        margin: 0 3px;
    }
    &__btn:hover {
        color: coral;
    }
    &__btn:focus {
        color: coral;
    }
}

.setting-bar {
    height: $tooblar-height;
    background-color: white;
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    box-shadow: 0 4px 5px gray;
    top: $tooblar-height;
}
